:root {
  --primary-color: #7895b2;
  --secondary-color: #e8dfca;
  --accent-color: #aebdca;
  --dark-accent: #f5efe6;
  --text-primary: #2c3333;
  --text-light: #ffffff;
  --shadow-color: rgba(0, 0, 0, 0.1);
  --card-bg: #ffffff;
  --menu-hover: #f8f9fa;
}

/* Base Styles */
img {
  display: block;
}

* {
  padding: 0;
  margin: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: var(--text-light);
  min-height: 100vh;
}

/* Main Content */
main {
  padding: 32px;
  flex: 1;
}

.content {
  margin: 0 auto;
  min-height: 100%;
}

.content .content__heading {
  font-weight: normal;
}

/* AppBar */
.app-bar {
  padding: 8px 16px;
  background-color: var(--primary-color);
  display: grid;
  grid-template-columns: auto 1fr auto;
  gap: 10px;
  position: sticky;
  top: 0;
  z-index: 99;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  color: var(--text-light);
}

.app-bar .app-bar__menu {
  display: flex;
  align-items: center;
  padding-left: 90%;
}

.app-bar .app-bar__menu button {
  background-color: transparent;
  border: none;
  font-size: 30px;
  cursor: pointer;
  min-width: 44px;
  min-height: 44px;
  color: var(--text-light);
}

.app-bar .app-bar__brand {
  display: flex;
  align-items: center;
}

.app-bar .app-bar__brand h1 {
  color: var(--text-light);
  font-size: 22px;
  user-select: none;
}

.app-bar .app-bar__navigation {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 150px;
  transition: all 0.3s;
  padding: 8px;
  background-color: var(--primary-color);
  overflow: hidden;
}

.app-bar .app-bar__navigation.open {
  left: 0;
}

.app-bar .app-bar__navigation ul li a {
  display: inline-block;
  text-decoration: none;
  padding: 0.5rem;
  border-radius: 999px;
  font-size: 1rem;
  font-weight: 600;
  color: var(--text-light);
  transition: all 0.2s ease-in-out;
  min-width: 44px;
  min-height: 44px;
}

.app-bar .app-bar__navigation ul li a:hover {
  background-color: var(--accent-color);
}

/* Explore */
.explore {
  text-align: center;
  font-weight: 800;
  font-size: 1.2rem;
  line-height: 9rem;
  padding-top: 2em;
  color: var(--primary-color);
}

/* Restaurants Grid */
.restaurants {
  display: grid;
  grid-template-columns: 1fr;
  gap: 30px;
  margin: 32px 0;
}

/* Restaurant Item */
.restaurant-item {
  width: 100%;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  overflow: hidden;
  background-color: var(--text-light);
}

.restaurant-item__header {
  position: relative;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__poster {
  width: 100%;
}

.restaurant-item .restaurant-item__header .restaurant-item__header__rating {
  position: absolute;
  padding: 8px;
  bottom: 20px;
  right: 0;
  display: inline-block;
  background-color: var(--primary-color);
  border: none;
  border-radius: 50%;
  color: var(--text-light);
}

.restaurant-item
  .restaurant-item__header
  .restaurant-item__header__rating
  .restaurant-item__header__rating__score {
  margin-right: 10px;
}

.restaurant-item .restaurant-item__content {
  padding: 16px;
}

.restaurant-item .restaurant-item__content h3 {
  margin: 0 0 10px 0;
  text-align: center;
  font-weight: 700;
}

.restaurant-item .restaurant-item__content h3 a {
  color: var(--text-primary);
  text-decoration: none;
  display: inline-block;
  min-width: 44px;
  min-height: 44px;
}

.restaurant-item .restaurant-item__content p {
  color: #6c757d;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
}

/* Restaurant Detail */
.restaurant {
  margin: 2rem auto;
  width: 100%;
  max-width: 1200px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  padding: 0 2rem;
}

.restaurant .restaurant__poster {
  width: 100%;
  max-width: 600px;
  border-radius: 12px;
  box-shadow: 0 8px 16px var(--shadow-color);
  margin: 0 auto;
}

.restaurant .restaurant__info {
  background-color: var(--card-bg);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 12px var(--shadow-color);
}

.restaurant__info h4 {
  font-size: 1.4em;
  color: var(--text-primary);
  margin: 1.5rem 0 1rem;
  font-weight: 600;
}

.restaurant__title {
  text-align: center;
  font-size: 2.5em;
  font-weight: 700;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  position: relative;
  padding-bottom: 1rem;
}

.restaurant__title::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80px;
  height: 3px;
  background-color: var(--primary-color);
  border-radius: 2px;
}

.restaurant-label {
  font-size: 1.5em;
  color: var(--text-primary);
  font-weight: 600;
  margin: 2rem 0 1rem;
  padding-left: 1rem;
  border-left: 4px solid var(--primary-color);
}

/* Enhanced Menu Styles */
.menus {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1.5rem;
  padding: 1rem 0;
}

.menus li {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  height: auto;
  font-size: 1rem;
  line-height: 1.4;
  border-radius: 8px;
  background-color: var(--card-bg);
  box-shadow: 0 2px 8px var(--shadow-color);
  transition: all 0.3s ease;
  color: var(--text-primary);
  border: 1px solid var(--secondary-color);
}

.menus li:hover {
  transform: translateY(-3px);
  box-shadow: 0 4px 12px var(--shadow-color);
  background-color: var(--menu-hover);
}

/* Enhanced Review Styles */
.review-section {
  background-color: var(--card-bg);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 12px var(--shadow-color);
  margin-top: 2rem;
}

.review {
  background-color: var(--dark-accent);
  padding: 1.5rem;
  margin: 1rem 0;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.review:hover {
  transform: translateX(5px);
  box-shadow: 0 2px 8px var(--shadow-color);
}

.review-label {
  padding-top: 2em;
  font-size: 1.2em;
}

.name-review {
  font-size: 1.1em;
  font-weight: 600;
  color: var(--text-primary);
  margin-bottom: 0.5rem;
}

.date-review {
  font-size: 0.85em;
  color: #6c757d;
  margin-bottom: 1rem;
}

.description-review {
  font-size: 1em;
  line-height: 1.6;
  color: var(--text-primary);
}

/* Enhanced Review Form */
#review-form {
  background-color: var(--card-bg);
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0 4px 12px var(--shadow-color);
  width: 100%;
  max-width: 600px;
  margin: 2rem auto;
  border: none;
}

#review-form h3 {
  font-size: 1.8em;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  text-align: center;
  font-weight: 600;
}

#review-form input[type='text'],
#review-form textarea {
  width: 100%;
  padding: 0.8rem 1rem;
  border: 1px solid var(--secondary-color);
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  margin-bottom: 1rem;
  min-width: 44px;
  min-height: 44px;
}

#review-form input[type='text']:focus,
#review-form textarea:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: 0 0 0 3px rgba(120, 149, 178, 0.2);
}

#review-form button {
  background-color: var(--primary-color);
  color: var(--text-light);
  padding: 1rem 2rem;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  width: 100%;
  min-width: 44px;
  min-height: 44px;
}

#review-form button:hover {
  background-color: var(--accent-color);
  transform: translateY(-2px);
}

/* Like Button */
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--primary-color);
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  font-size: 18px;
  color: var(--text-light);
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.like:hover {
  background-color: var(--accent-color);
}

/* Footer */
footer {
  padding: 10px;
  background-color: var(--primary-color);
  margin-top: 3rem;
}

footer p {
  text-align: center;
  color: var(--text-light);
}

/* Responsive Design */
@media screen and (min-width: 650px) {
  .app-bar {
    grid-template-columns: 1fr auto;
    padding: 8px 32px;
  }

  .app-bar .app-bar__brand h1 {
    font-size: 1.5em;
  }

  .app-bar .app-bar__menu {
    display: none;
  }

  .app-bar .app-bar__navigation {
    position: static;
    width: 100%;
  }

  .app-bar .app-bar__navigation ul li {
    display: inline-block;
  }

  .app-bar .app-bar__navigation ul li a {
    display: inline-block;
    width: 120px;
    text-align: center;
    margin: 0;
  }

  .restaurants {
    grid-template-columns: 1fr 1fr;
  }

  .restaurant {
    grid-template-columns: 1fr 2fr;
  }

  .restaurant .restaurant__title {
    grid-column: 1 / -1;
  }

  .restaurant .restaurant__overview {
    grid-column: 1 / -1;
  }

  .menus {
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  }
}

@media screen and (min-width: 800px) {
  .app-bar .app-bar__brand h1 {
    font-size: 2em;
  }
}

@media screen and (min-width: 1200px) {
  .restaurants {
    grid-template-columns: repeat(3, 1fr);
  }

  .restaurant__wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}

@media screen and (min-width: 1600px) {
  .restaurants {
    grid-template-columns: repeat(4, 1fr);
  }
}
