d-color: var(--primary-color);\n  overflow: hidden;\n}\n\n.app-bar .app-bar__navigation.open {\n  left: 0;\n}\n\n.app-bar .app-bar__navigation ul li a {\n  display: inline-block;\n  text-decoration: none;\n  padding: 0.5rem;\n  border-radius: 999px;\n  font-size: 1rem;\n  font-weight: 600;\n  color: var(--text-light);\n  transition: all 0.2s ease-in-out;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.app-bar .app-bar__navigation ul li a:hover {\n  background-color: var(--accent-color);\n}\n\n/* Explore */\n.explore {\n  text-align: center;\n  font-weight: 800;\n  font-size: 1.2rem;\n  line-height: 9rem;\n  padding-top: 2em;\n  color: var(--primary-color);\n}\n\n/* Restaurants Grid */\n.restaurants {\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 30px;\n  margin: 32px 0;\n}\n\n/* Restaurant Item */\n.restaurant-item {\n  width: 100%;\n  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1);\n  border-radius: 8px;\n  overflow: hidden;\n  background-color: var(--text-light);\n}\n\n.restaurant-item__header {\n  position: relative;\n}\n\n.restaurant-item .restaurant-item__header .restaurant-item__header__poster {\n  width: 100%;\n}\n\n.restaurant-item .restaurant-item__header .restaurant-item__header__rating {\n  position: absolute;\n  padding: 8px;\n  bottom: 20px;\n  right: 0;\n  display: inline-block;\n  background-color: var(--primary-color);\n  border: none;\n  border-radius: 50%;\n  color: var(--text-light);\n}\n\n.restaurant-item\n  .restaurant-item__header\n  .restaurant-item__header__rating\n  .restaurant-item__header__rating__score {\n  margin-right: 10px;\n}\n\n.restaurant-item .restaurant-item__content {\n  padding: 16px;\n}\n\n.restaurant-item .restaurant-item__content h3 {\n  margin: 0 0 10px 0;\n  text-align: center;\n  font-weight: 700;\n}\n\n.restaurant-item .restaurant-item__content h3 a {\n  color: var(--text-primary);\n  text-decoration: none;\n  display: inline-block;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n.restaurant-item .restaurant-item__content p {\n  color: #6c757d;\n  overflow: hidden;\n  text-overflow: ellipsis;\n  display: -webkit-box;\n  -webkit-line-clamp: 4;\n  -webkit-box-orient: vertical;\n}\n\n/* Restaurant Detail */\n.restaurant {\n  margin: 2rem auto;\n  width: 100%;\n  max-width: 1200px;\n  display: grid;\n  grid-template-columns: 1fr;\n  gap: 2rem;\n  padding: 0 2rem;\n}\n\n.restaurant .restaurant__poster {\n  width: 100%;\n  max-width: 600px;\n  border-radius: 12px;\n  box-shadow: 0 8px 16px var(--shadow-color);\n  margin: 0 auto;\n}\n\n.restaurant .restaurant__info {\n  background-color: var(--card-bg);\n  padding: 2rem;\n  border-radius: 12px;\n  box-shadow: 0 4px 12px var(--shadow-color);\n}\n\n.restaurant__info h4 {\n  font-size: 1.4em;\n  color: var(--text-primary);\n  margin: 1.5rem 0 1rem;\n  font-weight: 600;\n}\n\n.restaurant__title {\n  text-align: center;\n  font-size: 2.5em;\n  font-weight: 700;\n  color: var(--text-primary);\n  margin-bottom: 1.5rem;\n  position: relative;\n  padding-bottom: 1rem;\n}\n\n.restaurant__title::after {\n  content: '';\n  position: absolute;\n  bottom: 0;\n  left: 50%;\n  transform: translateX(-50%);\n  width: 80px;\n  height: 3px;\n  background-color: var(--primary-color);\n  border-radius: 2px;\n}\n\n.restaurant-label {\n  font-size: 1.5em;\n  color: var(--text-primary);\n  font-weight: 600;\n  margin: 2rem 0 1rem;\n  padding-left: 1rem;\n  border-left: 4px solid var(--primary-color);\n}\n\n/* Enhanced Menu Styles */\n.menus {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));\n  gap: 1.5rem;\n  padding: 1rem 0;\n}\n\n.menus li {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  padding: 1rem;\n  height: auto;\n  font-size: 1rem;\n  line-height: 1.4;\n  border-radius: 8px;\n  background-color: var(--card-bg);\n  box-shadow: 0 2px 8px var(--shadow-color);\n  transition: all 0.3s ease;\n  color: var(--text-primary);\n  border: 1px solid var(--secondary-color);\n}\n\n.menus li:hover {\n  transform: translateY(-3px);\n  box-shadow: 0 4px 12px var(--shadow-color);\n  background-color: var(--menu-hover);\n}\n\n/* Enhanced Review Styles */\n.review-section {\n  background-color: var(--card-bg);\n  padding: 2rem;\n  border-radius: 12px;\n  box-shadow: 0 4px 12px var(--shadow-color);\n  margin-top: 2rem;\n}\n\n.review {\n  background-color: var(--dark-accent);\n  padding: 1.5rem;\n  margin: 1rem 0;\n  border-radius: 8px;\n  transition: all 0.3s ease;\n}\n\n.review:hover {\n  transform: translateX(5px);\n  box-shadow: 0 2px 8px var(--shadow-color);\n}\n\n.review-label {\n  padding-top: 2em;\n  font-size: 1.2em;\n}\n\n.name-review {\n  font-size: 1.1em;\n  font-weight: 600;\n  color: var(--text-primary);\n  margin-bottom: 0.5rem;\n}\n\n.date-review {\n  font-size: 0.85em;\n  color: #6c757d;\n  margin-bottom: 1rem;\n}\n\n.description-review {\n  font-size: 1em;\n  line-height: 1.6;\n  color: var(--text-primary);\n}\n\n/* Enhanced Review Form */\n#review-form {\n  background-color: var(--card-bg);\n  padding: 2rem;\n  border-radius: 12px;\n  box-shadow: 0 4px 12px var(--shadow-color);\n  width: 100%;\n  max-width: 600px;\n  margin: 2rem auto;\n  border: none;\n}\n\n#review-form h3 {\n  font-size: 1.8em;\n  color: var(--text-primary);\n  margin-bottom: 1.5rem;\n  text-align: center;\n  font-weight: 600;\n}\n\n#review-form input[type='text'],\n#review-form textarea {\n  width: 100%;\n  padding: 0.8rem 1rem;\n  border: 1px solid var(--secondary-color);\n  border-radius: 8px;\n  font-size: 1rem;\n  transition: all 0.3s ease;\n  margin-bottom: 1rem;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n#review-form input[type='text']:focus,\n#review-form textarea:focus {\n  outline: none;\n  border-color: var(--primary-color);\n  box-shadow: 0 0 0 3px rgba(120, 149, 178, 0.2);\n}\n\n#review-form button {\n  background-color: var(--primary-color);\n  color: var(--text-light);\n  padding: 1rem 2rem;\n  border: none;\n  border-radius: 8px;\n  font-size: 1rem;\n  font-weight: 600;\n  cursor: pointer;\n  transition: all 0.3s ease;\n  width: 100%;\n  min-width: 44px;\n  min-height: 44px;\n}\n\n#review-form button:hover {\n  background-color: var(--accent-color);\n  transform: translateY(-2px);\n}\n\n/* Like Button */\n.like {\n  width: 55px;\n  height: 55px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  background-color: var(--primary-color);\n  position: fixed;\n  bottom: 16px;\n  right: 16px;\n  border-radius: 50%;\n  border: 0;\n  font-size: 18px;\n  color: var(--text-light);\n  cursor: pointer;\n  transition: background-color 0.3s ease;\n}\n\n.like:hover {\n  background-color: var(--accent-color);\n}\n\n/* Footer */\nfooter {\n  padding: 10px;\n  background-color: var(--primary-color);\n  margin-top: 3rem;\n}\n\nfooter p {\n  text-align: center;\n  color: var(--text-light);\n}\n\n/* Responsive Design */\n@media screen and (min-width: 650px) {\n  .app-bar {\n    grid-template-columns: 1fr auto;\n    padding: 8px 32px;\n  }\n\n  .app-bar .app-bar__brand h1 {\n    font-size: 1.5em;\n  }\n\n  .app-bar .app-bar__menu {\n    display: none;\n  }\n\n  .app-bar .app-bar__navigation {\n    position: static;\n    width: 100%;\n  }\n\n  .app-bar .app-bar__navigation ul li {\n    display: inline-block;\n  }\n\n  .app-bar .app-bar__navigation ul li a {\n    display: inline-block;\n    width: 120px;\n    text-align: center;\n    margin: 0;\n  }\n\n  .restaurants {\n    grid-template-columns: 1fr 1fr;\n  }\n\n  .restaurant {\n    grid-template-columns: 1fr 2fr;\n  }\n\n  .restaurant .restaurant__title {\n    grid-column: 1 / -1;\n  }\n\n  .restaurant .restaurant__overview {\n    grid-column: 1 / -1;\n  }\n\n  .menus {\n    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));\n  }\n}\n\n@media screen and (min-width: 800px) {\n  .app-bar .app-bar__brand h1 {\n    font-size: 2em;\n  }\n}\n\n@media screen and (min-width: 1200px) {\n  .restaurants {\n    grid-template-columns: repeat(3, 1fr);\n  }\n\n  .restaurant__wrap {\n    display: grid;\n    grid-template-columns: repeat(2, 1fr);\n    gap: 2rem;\n  }\n}\n\n@media screen and (min-width: 1600px) {\n  .restaurants {\n    grid-template-columns: repeat(4, 1fr);\n  }\n}\n"],"sourceRoot":""}]);
